@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;500&display=swap);
.homepage {
    margin-top: 100px;
    height: 100%;
    width: 100%;
    display: block;
    justify-content: center;
}

.homepage__photo img {
    min-height: 100%;
    height: 440px;
    width: 100%;
    border-radius: 100%;
    border: 1px solid black;
    object-fit: cover;
}

.homepage__photo {
    width: 100%;
}

.homepage__content {
    width: calc(100%-30px*2);
    padding: 0 30px;
    font-family: 'Noto Sans JP', sans-serif;
}

.homepage__content h1 {
    font: normal normal normal 100px/1.4em Noto Sans JP,'Noto Sans JP',sans-serif;
    font-family: 'Noto Sans JP', sans-serif;
    margin: 0;
    padding: 0px;
}

.homepage__content p {
    font-weight: 100;
}

.homepage__buttons {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
}

.homepage__buttons_button {
    border-radius: 50%;
    height: 7rem;
    width: 7rem;
    border: 1px solid black;
    font-weight: 700;
    font-size: medium;
    cursor: pointer;
}

.homepage__buttons_button:hover {
    background-color: #f3f3f3;
}

.btn_orange {
    background-color: #eea400;
}

.btn_red {
    background-color: #ee2c00;
}

.btn_ocean {
    background-color: #65dbff;
}

@media (min-width: 710px) {
    .homepage {
        display: flex;
    }

    .homepage__photo {
        width: calc(50% - 25px * 2);;
        margin-right: 20px;
    }

    .homepage__content {
        width: calc(50% - 25px * 2);;
        padding: 0;
        margin-left: 20px;
    }
}
@media (min-width: 992px) {
    .homepage__photo {
        width: 40%;
    }

    .homepage__buttons_button {
        height: 8rem;
        width: 8rem;
    }

    .homepage__content {
        width: 40%;
    }
}
@media (min-width: 1200px) {
    .homepage__photo {
        width: 460px;
    }

    .homepage__content {
        width: 460px;
    }
}
.contact {
    width: 90%;
    margin: 60px auto;
    display: block;
    align-items: baseline;
}

.contact__none {
    display: none;
    transition: 1s linear;
}

.contact__info {
    width: 100%;
}

.contact__popup h3{
    display: flex;
    justify-content: space-between;
    margin: 0;
}

.contact__popup p{
    margin: 10px 0 0 0;
}

.contact__popup h3 strong {
 cursor: pointer;
}
.contact__popup {
    background-color: #f4f4f4;
    padding: 15px;
    position: fixed;
    top: 10%;
    left: 50%;
    -webkit-transform: translate(-50%, -10%);
            transform: translate(-50%, -10%);
    width: 85%;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.contact__form {
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
}

.contact__form_input label, .contact__form_input input {
    display: block;
}

.contact__form_input label {
    margin-bottom: 10px;
}

.contact__form_input input {
    width: 100%;
    height: 35px;
    background-color: #f4f4f4;
    border: 1px solid black;
    border-radius: 0;
    margin-bottom: 15px;
}

.contact__form_textarea label{
    margin-bottom: 10px;
}

.contact__form_textarea textarea {
    background-color: #f4f4f4;
    border: 1px solid black;
    border-radius: 0;
    width: 100%;
    margin-top: 10px;
}

.contact__form button {
    width: 200px;
    height: 40px;
    margin-top: 10px;
    background-color: #eea302;
    border: 1px solid black;
    border-radius: 0;
    cursor: pointer;
    float: right;
    font-size: 22px;
    font-weight: 700;
}

.contact__form_unit {
    display: block;
    justify-content: space-between;
}

.contact__info_email p {
    margin: 0;
}
.contact__info_email h3 {
    margin: 25px 0 5px 0;
}

@media (min-width: 370px) {
    .contact__popup {
        width: 300px;
    }
}

@media (min-width: 630px) {
    .contact__form_unit {
        width: 100%;
        display: flex;
    }
    .contact__form_input {
        width: 48%;
    }
    .contact {
        width: 90%;
        margin: 70px auto;
        display: flex;
    }
    .contact__form {
        width: 50%;
    }
    .contact__info {
        width: 50%;
    }
}

@media (min-width: 800px) {
    .contact {
        width: 70%;
    }
}

@media (min-width: 1200px) {
    .contact {
        width: 60%;
        margin: 70px auto;
        display: flex;
    }
    .contact__form {
        margin-top: 0;
        margin-bottom: 0;
    }
    .contact__form_unit {
        display: flex;
    }
    .contact__form_input input {
        height: 30px;
    }
    .contact__form {
        width: 50%;
    }

}
.projects {
    width: 90%;
    margin: 70px auto;
}

.projects a {
    text-decoration: none;
    color: black;
}

.projects__post {
    display: block;
    justify-content: space-between;
    margin-top: 70px;
}

.projects_title {
    font-size: 50px;
}

.projects__post_preview_image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.projects__post_info_desc {
    line-height: 1.7;
    font-weight: 300;
}

.projects__post_info_title {
    margin: 0;
    font-size: 28px;
    font-weight: 500;
}

.projects__post_preview {
    width: 100%;
}

.projects__post_info {
    width: 100%;
}

.loading {
    opacity: 0;
}

.loading .h2 {
    height: 40px;
    background-color: white;
    border-radius: 5px;
}

.loading p {
    height: 200px;
    background-color: white;
    border-radius: 5px;
    margin: 1em 0 0 0;
}

.loading .img {
    background-color: white;
    border-radius: 5px;
    border: 0;
}

@-webkit-keyframes blink {50% {
    opacity: 1;
  }}

@keyframes blink {50% {
    opacity: 1;
  }}
.loading { -webkit-animation: 1.5s blink  linear  infinite; animation: 1.5s blink  linear  infinite }

@media (min-width: 660px) {
    .projects__post {
        display: flex;
    } 
    .projects__post_preview_image {
        width: 70%;
        float: right;
    }
    .projects__post_preview {
        width: 50%;
    }
    .projects__post_info {
        width: 50%;
    }
}

@media (min-width: 1000px) {
    .projects {
        width: 80%;
    }
    .projects__post_preview_image {
        min-width: 350px;
    }
}

@media (min-width: 1200px) {
    .projects {
        width: 60%;
    }
}
.navbar {
    display: flex;
    position: relative;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
}

.navbar__logo {
    display: flex;
}

.navbar__logo a {
    text-decoration: none;
    display: flex;
    color: black;
    cursor: pointer;
}

.navbar__logo_iconPlace {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__logo_iconPlace_icon {
    height: 22px;
    width: 22px;
    background-color: #eea400;
    border-radius: 500px;
}

.navbar__logo_name {
    margin-left: 10px;
}

.navbar__logo_status {
    display: flex;
    margin-left: 10px;
    justify-content: center;
    align-items: center;
    font-weight: 100;
}

.navbar__links {
    display: none;
    justify-content: space-between;
}

.navbar__links_link a {
    text-decoration: none;
    color: #3e3e3e;
    padding: 10px;
}



.navbar__links_link {
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 10px;
    margin: 0 5px 0 5px;
    font-size: 25px;
    padding: 9%;
}

.navbar__links_link a:hover {
    color: grey;
}

.navbar__mobile {
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__links_mobile {
    position: fixed;
    height: 100%;
    width: 100%;
    padding-top: 40%;
    background-color: #f4f4f4;
    top: 50%;
    left: 50%;
    /* bring your own prefixes */
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 2; 
}

@media (min-width: 630px) {
    .border_link {
        padding: 10px 15px!important;
        border-left: 1px solid black;
        border-right: 1px solid black;
    }
    .navbar__links_link {
        display: flex;
        justify-content: center;
        align-items: center;line-height: 10px;
        margin: 0 5px 0 5px;
        font-size: 16px;
        padding: 0;
    }
    .navbar__mobile_icon {
        display: none;
    }
    .navbar__links {
        display: flex;
    }
    .navbar__mobile {
        display: none;
    }
}
.navbar__mobile_icon span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: black;
    border-radius: 2px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.navbar__mobile_icon {
    width: 30px;
    height: 18px;
    position: relative;
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
  }
.open {
  position: fixed;
  margin-right: 9%;
  z-index: 2;
}
#nav-icon1 span:nth-child(1) {
    top: 0px;
  }
  
  #nav-icon1 span:nth-child(2) {
    top: 8px;
  }
  
  #nav-icon1 span:nth-child(3) {
    top: 16px;
  }
  
  #nav-icon1.open span:nth-child(1) {
    top: 18px;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg);
  }
  
  #nav-icon1.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
  }
  
  #nav-icon1.open span:nth-child(3) {
    top: 18px;
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
  }
  
footer {
    display: block;
    position: relative;
    justify-content: space-between;
    width: calc(100%-30px*2);
    padding: 0 30px 0 30px;
    margin: 10% auto 40px auto;
    border-top: 1px solid grey;
}

.footer__links_link {
    text-decoration: none;
    color: black;
    margin-right: 5px;
    font-size: 18px;
}

.footer__email p, .footer__phone p {
    margin: 0;
}

.footer__email h4, .footer__phone h4, .footer__links h4 {
    margin-bottom: 10px;
}

@media (min-width: 500px) {
    footer {
        display: flex;
        width: calc(100% - 50px * 2);
        padding: 0;
    }
}
.about {
    width: 100%;
    margin: 70px 0;
}

.about__block {
    display: block;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.about__block_unit {
    width: 100%;
    font-weight: 200;
    font-size: 16px;
    line-height: 1.7;
}

.about__block_unit h1 {
    font-size: 50px;
    margin: 0;
}

.about__photo img{
    width: 90%;
    margin-left: 20px;
    object-fit: cover;
    height: 90%;
}

.about__block_unit_vision {
    font-size: 55px;
    line-height: 1;
    display: none;
}

@media (min-width: 660px) {
    .about {
        width: 90%;
        margin: 70px auto;
    }
    .about__block {
        display: flex;
        padding: 0;
    }
    .about__block_unit {
        width: 50%;
    }
    .about__block_unit_vision {
        display: block;
    }
}

@media (min-width: 800px) {
    .about {
        width: 80%;
    }
}

@media (min-width: 1200px) {
    .about {
        width: 60%;
    }
}

.project {
    max-width: 650px;
    margin: 70px auto;
    padding: 0 25px;
}

.project_title {
    font-size: 40px;
}

@media (min-width: 992px) {
    .project {
        padding: 0;
        margin-top: 100px;
    }
    .project_title {
        font-size: 55px;
    }
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f4f4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

